	        	<center><h1 class="flat display-1">Hidrova</h1></center>
	        	<p style="padding: 20px !important;font-size: 20px !important;">
					La cuenca del Rio de la plata involucra a los siguientes paises: Argentina, Brasil, Paraguay y Uruguay. Posee una superficie de 3.1 millones de km2, una poblacin de 100 millones de habitantes y es considerada una regin ms urbanizada de Amrica latina.  Sus rios ms importantes son el Paran, Paraguay y el Rio Uruguay... Estos Rios son vias fluviales excelentes para la navegacin, historicamente han servido para el transporte de mercaderia dentro y fuera de la regin.En la actualidad es posible navegar unos 2000km por el Ri paran desde San Pablo hasta cuidad del Este,unos 1300 km por el Ro Paraguay (desde el Ro Apal hasta caceres) y unos 500km por el Ro Uruguay (desde el Ro de la Plata hasta la cuidad de Salto)
					El proyecto de Hidrovia Paran - Paraguay es una mega obra de ingenieria   para aumentar la capacidad de trfico de productos, principalmente agricolas(Soja), minerales(hierro) y combustibles. Dicho sistema abarca unos 3440km continuos entre el puerto de cceres(Norte de brasil) y el puerto de nueva palmira en colonia (Uruguay)
					El proyecto est valorado en 1 billn de dlares, Debe garantizar la navegacin diurna y nocturna  y durante todo el ao de barcos formados por varias bolsas y un empujador. Estos barcos llegan a dimensiones de hasta 200m de largo, 16 de ancho y  3.5 de calado
					Evidentemente las condiciones naturales de los rios no permiten la navegacin de elementos tan grandes para garantizar los objetivos de navegacin, es necesario realizar grandes obras de ingeniera en los ecosistemas fluviales, entre lo que se incluyan enderezamiento y ensanchamiento de los cauces, Dragando permantemente, dinamitar afloramientos rocosos
	        	</p>
